main {
  display: grid;
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
  height: 85vh;
  min-width: 80vw;
  max-width: 100vw;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
}

main .box {
  padding: 40px 30px;
  -webkit-box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.15);
          box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.15);
  text-align: center;
  width: 60%;
  border-radius: 5px;
}

main .box .image {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 200px;
  height: 200px;
  border-radius: 1000px;
  border: 2px solid rgb(243, 63, 63);
  padding: 5px;
}

main .box .name {
  font-family: "Montserrat", sans-serif;
  font-size: 36px;
  color: rgb(54, 54, 54);
  margin-top: 10px;
}

main .box .address {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  color: rgb(136, 136, 136);
}

main .box .button {
  background: rgb(243, 63, 63);
  border: 2px solid rgb(243, 63, 63);
  border-radius: 100px;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  color: white;
  font-weight: 400;
  padding: 10px 30px;
  margin-top: 20px;
  cursor: pointer;
  -webkit-transition: -webkit-box-shadow 300ms ease-in-out;
  transition: -webkit-box-shadow 300ms ease-in-out;
  -o-transition: box-shadow 300ms ease-in-out;
  transition: box-shadow 300ms ease-in-out;
  transition: box-shadow 300ms ease-in-out, -webkit-box-shadow 300ms ease-in-out;
}

main .box .button:hover {
  -webkit-box-shadow: 0 0 8px 2px rgba(243, 63, 63, 0.5);
          box-shadow: 0 0 8px 2px rgba(243, 63, 63, 0.5);
}

@media only screen and (min-width: 1200px) {
  main .box {
    width: 40%;
  }
}



