.footer {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  color: rgb(243, 63, 63);
  font-size: 16px;
  text-align: center;
  padding: 0;
  margin-top: -10px;
}

.footer a,
.footer a:hover,
.footer a:focus,
.footer a:active,
.footer a:visited {
  text-decoration: underline;
  -webkit-text-decoration-style: dotted;
          text-decoration-style: dotted;
  color: rgb(243, 63, 63);
}



